// React
import { Component, Fragment } from "react";

// Components
import Section from "@layout/home/Home.Section";
import Information from "@layout/home/Home.Information";
import Membership from "@layout/home/Home.Membership";
import Business from "@layout/home/Home.Business";
import Testimonials from "@layout/home/Home.Testimonials";

// Librarys
import AOS from "aos";

// JS
import { scrollToSection } from "@assets/js/scrollTo";

// JSON
import allOmnilifeSections from "@assets/json/home/omnilife-sections.json";
import aboutOmnilifeProducts from "@assets/json/home/about-omnilife-products.json";
import omnilifeProducts from "@assets/json/home/omnilife-products.json";
import aboutSeytuProducts from "@assets/json/home/about-seytu-products.json";
import seytuProducts from "@assets/json/home/seytu-products.json";
import businessSections from "@assets/json/home/business-sections.json";
import testimonials from "@assets/json/testimonials.json";

testimonials.splice(6, testimonials.length);

const omnilifeSections = allOmnilifeSections.map((item, i) => (
  <Section key={i} {...item} />
));

class Home extends Component {
  componentDidMount() {
    AOS.init({
      once: true,
    });
    this.scroller = scrollToSection({
      section: "navigation",
      scrollPosition: 10,
      whenLoadPage: true,
    });
  }
  componentWillUnmount() {
    this.scroller && clearTimeout(this.scroller);
  }
  render() {
    return (
      <Fragment>
        {omnilifeSections}
        <Information
          {...aboutOmnilifeProducts}
          id="shortcut-1DA21"
          category="omnilife"
          products={omnilifeProducts}
        />
        <Membership />
        <Business items={businessSections} />
        <Information
          {...aboutSeytuProducts}
          id="shortcut-D12H3"
          category="seytu"
          products={seytuProducts}
        />
        <Testimonials items={testimonials} />
      </Fragment>
    );
  }
}

export default Home;
