// React
import { Component } from "react";

// Styles
import "@styles/home/home.section.css";
import { scrollToSection } from "@assets/js/scrollTo";
import { Link } from "react-router-dom";

class Section extends Component {
  static defaultProps = {
    img: {
      alt: "img",
      width: 300,
      height: 300,
    },
  };
  shouldComponentUpdate() {
    return false;
  }
  render() {
    const {
      title,
      description,
      img,
      linked,
      containerStyle,
      wrapperStyle,
      button,
    } = this.props;
    return (
      <section className="about-omnilife" style={containerStyle}>
        <div className="container" style={wrapperStyle}>
          <div className="wrapper" data-aos="zoom-in" data-aos-duration={3000}>
            <article className="info" style={title.containerStyle}>
              <h2 style={title.style} className="title">
                {title.name}
              </h2>
              <p style={description.style} className="description">
                {description.name}
              </p>
              {linked.path ? (
                <Link
                  to={linked.path}
                  className="more-information"
                  style={button.style}
                >
                  Ms informacin
                </Link>
              ) : (
                <button
                  onClick={() =>
                    scrollToSection({ section: linked, duration: 2000 })
                  }
                  className="more-information"
                  style={button.style}
                >
                  Ms informacin
                </button>
              )}
            </article>
            <figure
              style={img.containerStyle}
              // data-aos="zoom-in"
              // data-aos-duration={2000}
            >
              <img
                src={require("@assets/img" + img.path).default}
                alt={img.alt}
                width={img.width}
                height={img.height}
              />
            </figure>
          </div>
        </div>
      </section>
    );
  }
}

export default Section;
