// React
import { Component } from "react";

// Librarys
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Styles
import "@styles/404/styles.pageNotFound.css";

const ghost = require("@assets/img/404/ghost.webp").default;
const magnifyingGlass = require("@assets/img/404/magnifying-glass.webp").default;

class PageNotFound extends Component {
  componentDidMount() {
    window.onload = function() {
      document.getElementById("root").className = "default"
    }
  }

  componentWillUnmount() {
    document.getElementById("root").removeAttribute("class")
  }

  render() {
    return (
      <div id="page-not-found">
        <img
          id="magnifying-glass"
          src={magnifyingGlass}
          alt="lupa"
          width="340"
          height="314"
        />

        <div className="wrapper">
          <figure>
            <img
              src={ghost}
              alt="ghost"
              width="250"
              height="250"
            />
          </figure>
          <h1 className="type-error">404</h1>
          <h3 className="title">Pgina no encontrada...</h3>
          <p className="message">
            No encuentras la pgina? - Comprueba si la url est bien escrita o
            vuelve al Inicio desde este enlace:
          </p>
          <Link to="/" className="go-to-home">
            <FontAwesomeIcon icon="share" />&nbsp;&nbsp;Volver al inicio
          </Link>
        </div>
      </div>
    );
  }
}

export default PageNotFound;
