// React
import { Component } from "react";

// Librarys
import { Link } from "react-router-dom";

// Styles
import "@styles/home/home.membership.css";

class Afiliate extends Component {
  render() {
    return (
      <div className="membership">
        <div
          className="wrapper"
          id="afiliacion"
          data-aos="fade-right"
          data-aos-easing="ease-in-sine"
          data-aos-duration={3000}
        >
          <h1 className="title">nete al mejor programa de emprendedores!</h1>

          <span className="text">
            S un emprendedor Omnilife y comienza a ganar dinero ya.
          </span>

          <Link
            className="link"
            id="programa-de-afiliados"
            to="/programa-de-afiliados"
          >
            Afiliarme ahora!
          </Link>
        </div>
      </div>
    );
  }
}

export default Afiliate;
