// React
import { Component, Fragment } from "react";

// Librarys
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Styles
import "@styles/styles.footer.css";

class Footer extends Component {
  static defaultProps = {
    sections: [],
  };
  render() {
    return (
      <footer>
        <div className="footer-content">
          {this.props.sections.map((item, i) => (
            <Section key={i} {...item} />
          ))}
        </div>

        <p id="copyright">&copy; Pgina web hecha por <a id="developer" href="https://github.com/Husdady" target="_blank" rel="noreferrer">Husdady</a></p>
      </footer>
    );
  }
}

export default Footer;

class Section extends Component {
  static defaultProps = {
    items: [],
  };
  render() {
    const { title, items, type } = this.props;
    if (items.length > 0) {
      this.content = (
        <ul className="wrapper-links">
          {items.map((item, i) => (
            <li className="link" key={i}>
              <FontAwesomeIcon icon={item.icon} />
              {
                type === "text"
                ? <span>&nbsp;&nbsp;{item.name}</span>
                : <Link to={item.path} target={item.path?.target}>&nbsp;&nbsp;{item.name}</Link>
              } 
            </li>
          ))}
        </ul>
      );
    } else {
      this.content = (
        <Fragment>
          <h5 className="subtitle">Cliente admirable:</h5>
          <a className="register pointer" href={this.props.clientLink} target="_blank" rel="noreferrer">
            Registrarme ahora!
          </a>
          <h5 className="subtitle">Empresario Omnilife:</h5>
          <a className="register pointer" href={this.props.businessmanLink} target="_blank" rel="noreferrer">
            Registrarme ahora!
          </a>
        </Fragment>
      );
    }
    return (
      <section className={this.props.classContainer}>
        <h4 className="title">- {title} -</h4>
        {this.content}
      </section>
    );
  }
}
