// React
import { Component } from "react";

// Components
import Testimony from "@layout/testimonials/Testimony";

// Styles
import "@styles/home/home.testimonials.css";

class Testimonials extends Component {
  render() {
    return (
      <div className="testimonials">
        <div className="wrapper" id="testimonios">
          <h2 className="title">Testimonios Omnilife</h2>
          <div className="testimonials-content">
            {this.props.items.map((item, i) => (
              <Testimony key={i} {...item} position={i + 1} />
            ))}
          </div>
        </div>
      </div>
    );
  }
}

export default Testimonials;
