// React
import { Component, Fragment } from "react";

// Components
import Header from "@layout/Header";
import Footer from "@layout/Footer";
import PageNotFound from "@layout/404/PageNotFound";

// Librarys
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  fab,
  faFacebookSquare,
  faWhatsapp,
} from "@fortawesome/free-brands-svg-icons";
import { far } from "@fortawesome/free-regular-svg-icons";
import {
  faHome,
  faAppleAlt,
  faShoppingBag,
  faShoppingCart,
  faEnvelope,
  faUsers,
  faUserAlt,
  faHandHoldingUsd,
  faGift,
  faPlane,
  faTh,
  faCheckCircle,
  faCaretDown,
  faBars,
  faTimes,
  faShare,
  faStar
} from "@fortawesome/free-solid-svg-icons";

// Styles
import "@styles/home/home.business.css";

// Pages
import { Home, ProductosOmnilife, ProductosSeytu } from "@pages";

// JS
import { PATH } from "@assets/js/keys";
import { getKey, setKey } from "@assets/js/localStorage";

// JSON
import footerSections from "@assets/json/footer-sections";
const Product = () => <p>la maita</p>;
const ProgramaDeAfiliados = () => <p>programa de afiliados</p>;
const Contacto = () => <p>contacto</p>;
const Testimonials = () => <p>testimonios</p>;

library.add(
  fab,
  far,
  faTh,
  faHome,
  faAppleAlt,
  faShoppingBag,
  faShoppingCart,
  faUsers,
  faUserAlt,
  faHandHoldingUsd,
  faFacebookSquare,
  faWhatsapp,
  faEnvelope,
  faGift,
  faPlane,
  faCheckCircle,
  faCaretDown,
  faBars,
  faTimes,
  faShare,
  faStar,
);

class App extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Page path="/" component={Home} />
          <Page path="/productos-omnilife" component={ProductosOmnilife} />
          <Page path="/productos-omnilife/:product" component={Product} />
          <Page path="/productos-seytu" component={ProductosSeytu} />
          <Page path="/productos-seytu/:product" component={Product} />
          <Page path="/programa-de-afiliados" component={ProgramaDeAfiliados} />
          <Page path="/contacto" component={Contacto} />
          <Page path="/testimonios" component={Testimonials} />
          <Route exact path="/pagina-no-encontrada" component={PageNotFound} />
          <Redirect to="/pagina-no-encontrada" />
        </Switch>
      </Router>
    );
  }
}

export default App;

class Page extends Component {
  static defaultProps = {
    exact: true,
  };

  async shouldComponentUpdate(nextProps) {
    const savedPath = await getKey(PATH);
    return savedPath !== nextProps.path
  }

  render() {
    setKey(PATH, this.props.path);
    return (
      <Fragment>
        <Header />
        <Route {...this.props} />
        <Footer sections={footerSections} />
      </Fragment>
    );
  }
}
