// React
import { Component } from "react";

// Librarys
import { Link } from "react-router-dom";

// Styles
import "@styles/testimonios/styles.testimony.css";

class Testimony extends Component {
  static defaultProps = {
    author: {
      img: {
        width: 239,
        height: 239,
      },
      cutLength: 100,
    },
  };
  render() {
    const { author } = this.props;
    const imgAlt = author.name?.replace(/\s/gi, "-")?.toLowerCase();

    if (author["testimony"].length > 50) {
      author["testimony"] = author["testimony"].substring(0, author.cutLength) + "....";
    }

    return (
      <section className="testimony" data-aos="fade-up" data-aos-duration={3000} data-aos-delay={this.props.position * 500}>
        <img
          src={require("@assets/img" + author.img.path).default}
          title={author.name}
          alt={imgAlt}
          width={150}
          height={150}
        />

        <p className="author-testimony">{author.testimony}</p>
        <h3 className="author">- {author.name} -</h3>
        <Link to="/testimonios" className="read-more">
          Leer ms
        </Link>
      </section>
    );
  }
}

export default Testimony;
