// React
import { Component, Fragment } from "react";

// Components
import Product from "./Home.Product";

// Librarys
import { Link } from "react-router-dom";

// CSS
import "@styles/home/home.information.css";

class Information extends Component {
  static defaultProps = {
    img: {
      width: 1920,
      height: 1080,
    },
    questions: [],
    button: {
      link: "/",
    },
    products: [],
  };
  render() {
    const {
      title,
      img,
      questions,
      button,
      products,
      containerStyle,
      wrapperStyle,
    } = this.props;
    return (
      <div
        id={this.props.id}
        className="about-products animate__animated animate__bounce animate__slower"
        category={this.props.category}
        style={containerStyle}
      >
        <div className="wrapper" style={wrapperStyle}>
          <h1 className="title" style={title.style}>
            {title.name}
          </h1>

          <figure>
            <img
              src={require("@assets/img" + img.path).default}
              alt={img.alt}
              width={1280}
              height={1024}
            />
          </figure>

          {questions.map((question, i) => (
            <Fragment key={i}>
              <h3 className="question">{question.name}</h3>
              <p className="response">{question.response}</p>
            </Fragment>
          ))}

          <Link
            to={button.link}
            className="see-all-products"
            style={button.style}
          >
            {button.title}
          </Link>
        </div>

        <div className="products">
          {products.map((item, i) => (
            <Product {...item} category={this.props.category} key={i} />
          ))}
        </div>
      </div>
    );
  }
}

export default Information;
