// React
import { Component } from "react";

// Librarys
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle } from "@fortawesome/free-solid-svg-icons";

// Styles
import "@styles/home/home.product.css"

class Product extends Component {
  static defaultProps = {
    img: {
      path: "#",
      alt: "producto",
      width: 400,
      height: 400
    },
    benefits: [],
    productLink: "/",
  };

  render() {
    const { title, img, benefits, buttonStyle, productLink } = this.props;
    const imgAlt = title.name.toLowerCase();
    const productBenefits = benefits.map((benefit, i) => (
      <li key={i} className="benefit">
        <FontAwesomeIcon icon={faCheckCircle} />
        &nbsp;{benefit.name}
      </li>
    ));
    return (
      <section className="product" category={this.props.category}>
        <h4 className="title" style={title.style}>{title.name}</h4>
        <img
          src={require("@assets/img" + img.path).default}
          title={title.name}
          alt={imgAlt}
          width={400}
          height={400}
        />
        <ul className="wrapper">{productBenefits}</ul>
        <Link to={productLink} style={buttonStyle}>Ver ms</Link>
      </section>
    );
  }
}

export default Product;
