// React
import { Component } from 'react';

// Components
import ProductsContainer from "@layout/products/ProductsContainer";

// Styles
import "@styles/productos/styles.omnilifeProducts.css"

class ProductosOmnilife extends Component {
  render() { 
    return (
      <ProductsContainer
        category="omnilife-products"
        products={[]}
      />
    );
  }
}
 
export default ProductosOmnilife;
